import * as React from 'react';
import { insertHr } from '../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export function HrMobile(props: MobileToolbarButtonProps) {
  const { Wrapper, item, controller, locale } = props;
  const handleClick = React.useCallback(() => {
    controller.run('onAction', insertHr({}));
  }, []);

  return (
    <Wrapper
      item={item}
      type="line"
      onClick={handleClick}
      icon="hr"
      lineText={locale?.quickInsertTitle}
    />
  );
}
